import React, { useState, useEffect } from 'react'
import { Link } from 'react-router-dom'
import Modal from 'react-modal'
import { UserContext } from './components/User'
import { makeStyles } from '@material-ui/core/styles'
import Input from '@material-ui/core/Input'
import shareIcon from './images/share.svg'
import LoadingComponent from './reactComponents/loading'
import OfficialLine from './reactComponents/OfficialLine'

const useStyles = makeStyles(theme => ({
  container: {
    display: 'flex',
    flexWrap: 'wrap',
  },
  input: {
    margin: theme.spacing(1),
  },
}))
const customStyles = {
  content: {
    top: '50%',
    left: '50%',
    right: 'auto',
    bottom: 'auto',
    marginRight: '-50%',
    transform: 'translate(-50%, -50%)',
  },
}
Modal.setAppElement('#root')
function RenewSetting(props) {
  const { state, dispatch } = React.useContext(UserContext)
  const [nowloading, setNowloading] = useState(true) //
  const [inputUsername, setInputUsername] = useState(state.user.username) //
  const [worktime, setWorktime] = useState(state.user.worktime) //
  const [isChangeWorktime, setIsChangeWorktime] = useState(false) //
  const [memberlist, setMenberList] = useState([]) //
  const [nonadministerlist, setNonadministerList] = useState([]) //
  const [administerlist, setAdministerList] = useState([]) //
  const [administeroption, setAdministerOption] = useState('') //
  const [memberModalOpen, setMenberModalOpen] = useState(false) //
  const [administerModalOpen, setAdministerModalOpen] = useState(false) //
  const [isAddAdminister, setIsAddAdminister] = useState(false) //
  const testrouter = ''
  useEffect(() => {
    const firstLoading = user => {
      setInputUsername(user.username)
      setWorktime(user.worktime)
      if (user.administer) {
        loadmemberlist()
      }
      setNowloading(false)
    }
    if (state.user.userId !== '') {
      firstLoading(state.user)
    } else {
      //statefetch
      fetch(testrouter + '/userdata')
        .then(res => res.json())
        .then(data => {
          dispatch({
            type: 'set-user',
            payload: {
              user: data,
            },
          })

          firstLoading(data)
        })
    }
  }, [state.user])
  //
  const loadmemberlist = () => {
    //
    fetch(testrouter + '/memberlist')
      .then(res => res.json())
      .then(list => {
        setMenberList(list)
        const adlist = []
        const nonadlist = []
        for (let user of list) {
          if (user.administer) {
            adlist.push(user)
          } else {
            nonadlist.push(user)
          }
        }
        setAdministerList(adlist)
        setNonadministerList(nonadlist)
        setAdministerOption(nonadlist[0])
      })
  }
  //
  const updateUsername = () => {
    if (state.user.username !== inputUsername) {
      if (inputUsername === '') {
        alert('')
      } else if (inputUsername.length > 10) {
        alert('10')
      } else {
        //fetch
        fetch(testrouter + '/updateusername', {
          method: 'POST',
          body: JSON.stringify([inputUsername]),
          headers: {
            'Content-Type': 'application/json',
          },
          mode: 'cors',
        })
          .then(res => res.json())
          .then(str => {
            alert('')
          })

        const user = state.user
        user.username = inputUsername
        dispatch({
          type: 'set-user',
          payload: {
            user: user,
          },
        })
      }
    }
  }
  //
  const resetWorktime = () => {
    if (state.user.worktime !== worktime) {
      //fetch
      fetch(testrouter + '/updateworktime', {
        method: 'POST',
        body: JSON.stringify([worktime]),
        headers: {
          'Content-Type': 'application/json',
        },
        mode: 'cors',
      }).then(res => res.json())
      const user = state.user
      user.worktime = worktime
      dispatch({
        type: 'set-user',
        payload: {
          user: user,
        },
      })
    }
    setIsChangeWorktime(false)
  }
  //
  const onClickunsubsribe = () => {
    //
    if (window.confirm('\n')) {
      fetch(testrouter + '/deleteuser', {
        method: 'POST',
        //body: "sub",
        headers: {
          'Content-Type': 'application/json',
        },
        mode: 'cors',
      }).then(res => res.json())
      fetch('/logout').then(res => res.json())
      props.history.push('/')
    }
  }
  //
  const onClickdeletemember = member => {
    if (
      window.confirm(
        member.name +
          '\n',
      )
    ) {
      //
      fetch(testrouter + '/deletemember', {
        method: 'POST',
        body: JSON.stringify([member.userid]),
        headers: {
          'Content-Type': 'application/json',
        },
        mode: 'cors',
      })
        .then(res => res.json())
        .then(res => {
          setMenberModalOpen(false)
          //
          loadmemberlist()
        })
    }
  }
  //
  const onClickdeleteadminister = member => {
    if (window.confirm(member.name + '')) {
      //
      fetch(testrouter + '/deleteadminister', {
        method: 'POST',
        body: JSON.stringify([member.userid]),
        headers: {
          'Content-Type': 'application/json',
        },
        mode: 'cors',
      })
        .then(res => res.json())
        .then(res => {
          setAdministerModalOpen(false)
          //
          loadmemberlist()
        })
    } else {
    }
  }
  //
  const onChangeaddministeroption = e => {
    for (const member of nonadministerlist) {
      if (member.name === e.target.value) {
        setAdministerOption(member)
        break
      }
    }
  }
  //
  const onClickaddAdminister = () => {
    const member = administeroption
    if (window.confirm(member.name + '')) {
      //
      fetch(testrouter + '/addadminister', {
        method: 'POST',
        body: JSON.stringify([member.userid]),
        headers: {
          'Content-Type': 'application/json',
        },
        mode: 'cors',
      })
        .then(res => res.json())
        .then(res => {
          setAdministerModalOpen(false)
          setIsAddAdminister(false)
          //
          loadmemberlist()
        })
    }
  }
  return (
    <div>
      <h1>Settings</h1>
      {nowloading ? (
        <LoadingComponent />
      ) : (
        <div>
          <div className="profileholder">
            <div className="profilesetting">
              <div className="profilecard">
                <img
                  src={state.user.picture}
                  id="profileimage"
                  alt="profile"
                ></img>
                <p id="linename">{state.user.displayName}</p>
              </div>
              <div>
                
                <Input
                  id="username"
                  value={inputUsername}
                  onChange={e => setInputUsername(e.target.value)}
                  className={useStyles.input}
                  inputProps={{
                    'aria-label': 'username',
                  }}
                  placeholder="10"
                />
                <button
                  onClick={updateUsername}
                  className="bluebutton"
                  id="updatebutton"
                >
                  
                </button>
              </div>
              <div className="worktime">
                :
                {isChangeWorktime ? (
                  <span>
                    <input
                      type="radio"
                      name="worktime"
                      value=""
                      checked={worktime === ''}
                      onChange={() => setWorktime('')}
                    />
                    
                    <input
                      type="radio"
                      name="worktime"
                      value=""
                      checked={worktime === ''}
                      onChange={() => setWorktime('')}
                    />
                    
                    <button onClick={resetWorktime} className="bluebutton">
                      
                    </button>
                  </span>
                ) : (
                  <span>
                    {state.user.worktime}
                    <button
                      onClick={() => setIsChangeWorktime(true)}
                      className="bluebutton"
                    >
                      
                    </button>
                  </span>
                )}
              </div>
              <button
                onClick={onClickunsubsribe}
                className="redbutton"
                id="disbutton"
              >
                
              </button>
            </div>
          </div>
          <div className="manualdiv">
            <a
              href="https://docs.google.com/presentation/d/1d4WBzpo-So3vK8kw5eVjB_fQ_LBcnjHPEuaHL-7Q_1g/edit?usp=sharing"
              target="_blank"
              rel="noopener noreferrer"
            >
              <span className="outlinkButton">
                
                <img src={shareIcon} id="shareIcon" alt="" />
              </span>
              <p id="sharesubtitle"></p>
            </a>
          </div>
          <div
            className="manualdiv"
            onClick={e => props.history.push('/Contact')}
          >
            <span></span>
            <p id="sharesubtitle"></p>
          </div>
          <div
            className="manualdiv"
            onClick={e => props.history.push('/Game/CountGame')}
          >
            <span></span>
            <p id="sharesubtitle"></p>
          </div>
          <OfficialLine />
          <div
            style={{ display: state.user.administer ? '' : 'none' }}
            className="administerview"
          >
            <h2 className="administerheader"></h2>
            <Link to="/Wishlist">
              <span className="administerbutton" id="wishlistbutton">
                
              </span>
            </Link>
            <br />
            <button
              onClick={() => props.history.push('/ShiftAdministar')}
              className="administerbutton"
              style={{ width: '250px' }}
            >
              
            </button>
            <br />
            <button
              onClick={() => props.history.push('/Information')}
              className="administerbutton"
              style={{ width: '250px' }}
            >
              
            </button>
            <br />
            <button
              onClick={() => props.history.push('/Plans')}
              className="administerbutton"
              style={{ width: '250px' }}
            >
              
            </button>
            <br />
            <button
              onClick={() => props.history.push('/Log')}
              className="administerbutton"
              style={{ width: '250px' }}
            >
              
            </button>
            <br />
            <button
              onClick={() => props.history.push('/UploadImage')}
              className="administerbutton"
              style={{ width: '250px' }}
            >
              
            </button>
            <br />
            <button
              onClick={() => setMenberModalOpen(true)}
              className="administerbutton"
            >
              
            </button>
            <button
              onClick={() => setAdministerModalOpen(true)}
              className="administerbutton"
            >
              
            </button>
          </div>
          <Modal
            isOpen={memberModalOpen}
            onRequestClose={() => setMenberModalOpen(false)}
            style={customStyles}
            contentLabel="Register Modal"
          >
            <h2 className="modaltitle"></h2>
            <div className="modalheight">
              {memberlist.map(member => (
                <div key={member.userid}>
                  {member.name}
                  <span
                    style={{
                      display:
                        member.userid !== state.user.userId ? '' : 'none',
                    }}
                  >
                    <button
                      className="redbutton"
                      value={member.userid}
                      onClick={e => onClickdeletemember(member)}
                    >
                      
                    </button>
                  </span>
                </div>
              ))}
            </div>
            <button
              className="redbutton"
              onClick={() => setMenberModalOpen(false)}
            >
              
            </button>
          </Modal>
          <Modal
            isOpen={administerModalOpen}
            onRequestClose={() => setAdministerModalOpen(false)}
            style={customStyles}
            contentLabel="Administer Modal"
          >
            <h2 className="modaltitle"></h2>
            <div className="modalheight">
              {administerlist.map(member => (
                <div key={member.userid}>
                  {member.name}
                  <span
                    style={{
                      display:
                        member.userid !== state.user.userId ? '' : 'none',
                    }}
                  >
                    <button
                      className="redbutton"
                      onClick={() => onClickdeleteadminister(member)}
                    >
                      
                    </button>
                  </span>
                </div>
              ))}
            </div>
            {isAddAdminister && nonadministerlist.length > 0 ? (
              <span>
                <select
                  className="timeselect"
                  onChange={onChangeaddministeroption}
                  value={administeroption.name}
                >
                  {nonadministerlist.map(member => (
                    <option>{member.name}</option>
                  ))}
                </select>
                <button className="bluebutton" onClick={onClickaddAdminister}>
                  
                </button>
              </span>
            ) : (
              <button
                className="bluebutton"
                onClick={e => setIsAddAdminister(true)}
              >
                
              </button>
            )}

            <button
              className="redbutton"
              onClick={() => setAdministerModalOpen(false)}
            >
              
            </button>
          </Modal>
        </div>
      )}
    </div>
  )
}

export default RenewSetting
