import React, { useState, useEffect } from 'react'
import { UserContext } from './components/User'
import Dropzone from 'react-dropzone'
import './css/UploadImage.css'
import LoadingComponent from './reactComponents/loading'

export default function UploadImage(props) {
  const { state, dispatch } = React.useContext(UserContext)
  const [consoleText, setConsoleText] = useState('')
  const [nowUploading, setNowUploading] = useState(false)
  const [addImage, setAddImage] = useState('')
  const [nowSelect, setNowSelect] = useState(true)
  const testrouter = ''
  useEffect(() => {
    const firstLoading = user => {
      //
      //console.log(user)
    }
    if (state.user.userId !== '') {
      firstLoading(state.user)
    } else {
      //statefetch
      fetch(testrouter + '/userdata')
        .then(res => res.json())
        .then(data => {
          dispatch({
            type: 'set-user',
            payload: {
              user: data,
            },
          })
          firstLoading(data)
        })
    }
  }, [state.user])

  //upload
  const handleOnDrop = files => {
    setNowUploading(true)
    Promise.all(files.map((file, index) => uploadImage(file, index)))
      .then(images => {
        console.log(images)
        //
        /*
        this.setState({
          isUploading: false,
          images: this.state.images.concat(images),
          file2ndname: this.state.file2ndname.concat(images[0].file.name),
        })
        */
      })
      .catch(e => console.log(e))
  }
  //upload POST
  const uploadImage = (file, index) => {
    //
    if (0 !== index) {
      return
    }
    //handling
    if ('' !== addImage) {
      //fetch
      fetch('/image/deleteNewImage', {
        method: 'POST',
        body: JSON.stringify({ name: addImage.filename }),
        headers: {
          'Content-Type': 'application/json',
        },
        mode: 'cors',
      })
        .then(res => res.json())
        .then(res => {
          if ('clear' === res) {
            //setAddImage('')
          } else {
            setConsoleText('')
          }
        })
    }
    //post
    const imagedata = new FormData()
    imagedata.append('imagedata', file)
    fetch('/image/uploadImage', {
      method: 'POST',
      body: imagedata,

      mode: 'cors',
    })
      .then(res => res.json())
      .then(res => {
        //console.log(res)
        setAddImage(res)
        setNowUploading(false)
      })

    const name = file.name
    return {
      name,
    }
  }

  //
  const OnSelectImage = bool => {
    setNowSelect(bool)
  }

  //
  const OnClickSetImage = () => {
    if (nowSelect) {
      //
      //
      if ('' !== addImage) {
        //POST
        fetch('/image/deleteNewImage', {
          method: 'POST',
          body: JSON.stringify({ name: addImage.filename }),
          headers: {
            'Content-Type': 'application/json',
          },
          mode: 'cors',
        })
          .then(res => res.json())
          .then(res => {
            if ('clear' === res) {
              setAddImage('')
            } else {
              setConsoleText('')
            }
          })
      }
    } else {
      //
      //null or new
      if ('' === addImage) {
        //null
        if ('' !== state.user.imagepath) {
          //def
          //post
          fetch('/image/deleteDefaultImage', {
            method: 'POST',
            body: JSON.stringify({ path: state.user.imagepath }),
            headers: {
              'Content-Type': 'application/json',
            },
            mode: 'cors',
          })
            .then(res => res.json())
            .then(res => {
              if ('clear' === res) {
                const user = state.user
                user.imagepath = ''
                dispatch({
                  type: 'set-user',
                  payload: {
                    user: user,
                  },
                })
              } else {
                setConsoleText('')
              }
            })
        }
      } else {
        //new
        if ('' !== state.user.imagepath) {
          //
          //DB

          fetch('/image/deleteDefaultImageToNew', {
            method: 'POST',
            body: JSON.stringify({
              path: state.user.imagepath,
              newpath: addImage.path,
            }),
            headers: {
              'Content-Type': 'application/json',
            },
            mode: 'cors',
          })
            .then(res => res.json())
            .then(res => {
              if ('clear' === res) {
                const user = state.user
                user.imagepath = addImage.path
                dispatch({
                  type: 'set-user',
                  payload: {
                    user: user,
                  },
                })
                setAddImage('')
                setNowSelect(true)
              } else {
                setConsoleText('')
              }
            })
        } else {
          //
          //DB
          const paths = addImage.path.split('/')
          const filename = paths[paths.length - 1]
          console.log(addImage.path)
          fetch('/image/setNewImage', {
            method: 'POST',
            body: JSON.stringify({ name: filename, newpath: addImage.path }),
            headers: {
              'Content-Type': 'application/json',
            },
            mode: 'cors',
          })
            .then(res => res.json())
            .then(res => {
              if ('clear' === res) {
                const user = state.user
                user.imagepath = addImage.path
                dispatch({
                  type: 'set-user',
                  payload: {
                    user: user,
                  },
                })
                setAddImage('')
                setNowSelect(true)
              } else {
                setConsoleText('')
              }
            })
        }
        //console.log('new')
      }
    }
  }

  //
  const OnClickSetDefaultImage = () => {
    if ('' !== state.user.imagepath) {
      //def
      //post
      fetch('/image/deleteDefaultImage', {
        method: 'POST',
        body: JSON.stringify({
          path: state.user.imagepath,
        }),
        headers: {
          'Content-Type': 'application/json',
        },
        mode: 'cors',
      })
        .then(res => res.json())
        .then(res => {
          if ('clear' === res) {
            const user = state.user
            user.imagepath = ''
            dispatch({
              type: 'set-user',
              payload: {
                user: user,
              },
            })
          } else {
            setConsoleText('')
          }
        })
    }
  }

  return (
    <div
      className="background"
      style={
        nowSelect
          ? {
              backgroundImage: `url(${state.user.imagepath})`,
            }
          : {
              backgroundImage: `url(${addImage.path})`,
            }
      }
    >
      <h1>UploadImage</h1>
      {state.user.userId !== '' ? (
        <div>
          <p>{consoleText}</p>
          <div id="upload-area">
            {nowUploading ? (
              <div></div>
            ) : (
              <Dropzone onDrop={handleOnDrop} accept="image/*">
                {({ getRootProps, getInputProps }) => (
                  <div id="drop" {...getRootProps()}>
                    <input {...getInputProps()} />
                    <div></div>
                    <div></div>
                  </div>
                )}
              </Dropzone>
            )}
          </div>
          <div id="background-sample-two">
            <div
              className="background-sample-flame"
              id={nowSelect ? 'background-sample-select' : ''}
              onClick={() => OnSelectImage(true)}
            >
              {state.user == '' || '' === state.user.imagepath ? (
                <p></p>
              ) : (
                <img
                  src={state.user.imagepath}
                  className="background-sample"
                  alt=""
                />
              )}
            </div>
            {addImage === '' ? (
              <div
                className="background-sample-flame"
                id={!nowSelect ? 'background-sample-select' : ''}
                onClick={() => OnSelectImage(false)}
              >
                
              </div>
            ) : (
              <div
                className="background-sample-flame"
                id={!nowSelect ? 'background-sample-select' : ''}
                onClick={() => OnSelectImage(false)}
              >
                <img
                  src={addImage.path}
                  className="background-sample"
                  alt=""
                />
              </div>
            )}
          </div>
          <p></p>
          <button
            className="bluebutton"
            onClick={OnClickSetImage}
            style={{ width: 100 }}
          >
            
          </button>
          <button
            className="redbutton"
            onClick={OnClickSetDefaultImage}
            style={{ width: 100 }}
          >
            
          </button>
        </div>
      ) : (
        <LoadingComponent />
      )}
    </div>
  )
}
