import React, { useState } from 'react'
//import NewCalender from './Calender'
//import request from 'superagent'
import { UserContext } from './components/User'
import './css/Test.css'
import Image from './images/adface.png'
import sImage from './images/wakana.jpg'

function TestPage(props) {
  const { state, dispatch } = React.useContext(UserContext)
  const [image, setImage] = useState('TestImageOne')
  const isBool = true
  React.useEffect(() => {}, [state.user])
  const resetColor = () =>
    dispatch({
      type: 'reset-user',
    })

  // "chnage-color"dispatch
  // payload
  const setColor = color => () =>
    dispatch({
      type: 'set-user',
      payload: {
        user: { username: 'name', userId: 'sampleId', picture: 'http://' },
      },
    })

  return (
    <div
      id="background-test"
      style={
        isBool
          ? {
              backgroundImage: `url()`,
            }
          : {
              backgroundImage: `url(${sImage})`,
            }
      }
    >
      <h1>TestPage</h1>
      <p></p>
      <ul>
        <li>: {state.user.username}</li>
        <li>: {state.user.userId}</li>
        <li>: {state.user.picture}</li>
      </ul>
      <button
        onClick={setColor({
          name: 'Dark',
          text: '#ffffff',
          back: '#000000',
        })}
      >
        
      </button>
      <button onClick={resetColor}></button>
      <div>
        
        <button onClick={() => setImage('TestImageOne')}>one</button>
        <button onClick={() => setImage('TestImageTwo')}>two</button>
        <div className={image}></div>
        <div>test</div>
      </div>
      <div></div>
      <img src={Image} alt="test" />
    </div>
  )
}
export default TestPage
