import React, { useState, useEffect, useContext } from 'react'
import Modal from 'react-modal'
import SimpleDatePicker from './PlanDatePicker'
import Calender from './Calender'
import { GetFormatDate } from './DateHandler'
import { UserContext } from './components/User'
const customStyles = {
  content: {
    top: '50%',
    left: '50%',
    right: 'auto',
    bottom: 'auto',
    marginRight: '-50%',
    transform: 'translate(-50%, -50%)',
  },
  formcontrol: {
    fontSize: '10px',
  },
}

const Plans = props => {
  const { state, dispatch } = useContext(UserContext)
  const [plans, setPlandata] = useState([])
  const [plansArray, setPlanArray] = useState([])
  const [selectdate, setSelectDate] = useState(new Date())
  const [addplanText, setAddplanText] = useState('')
  const [isOpenModal, setOpenModal] = useState(false)
  const [changePlandate, setChangePlandate] = useState('')
  const [changePlantext, setChangePlantext] = useState('')
  const [printDate, setPrintdate] = useState(new Date('2019', '11', '01'))
  const testroute = ''
  useEffect(() => {
    /*
    fetch(testroute + '/userdata')
      .then(res => res.json())
      .then(data => {
      */
    if (state.user.userId !== '') {
      if (state.user.administer) {
        //
        loadPlans()
      } else {
        //
        alert('')
        props.history.push('/')
      }
    } else {
      //statefetch
      fetch(testroute + '/userdata')
        .then(res => res.json())
        .then(data => {
          dispatch({
            type: 'set-user',
            payload: {
              user: data,
            },
          })
          if (data.administer) {
            //
            loadPlans()
          } else {
            //
            alert('')
            props.history.push('/')
          }
        })
    }
    //})
  }, [])
  const loadPlans = () => {
    fetch(testroute + '/plandata')
      .then(res => res.json())
      .then(data => setPlans(data))
  }
  //Planlist
  const setPlans = plans => {
    const list = []
    const array = []
    for (let plan of plans) {
      list[plan.date] = { text: plan.text, date: plan.date }
      array.push({ text: plan.text, date: plan.date })
    }
    setPlanArray(array)
    setPlandata(list)
  }
  const onClickAddPlan = () => {
    //
    if (addplanText === '') {
      alert('')
      return
    }
    const adddate = GetFormatDate(selectdate)
    const data = {
      text: addplanText,
      date: adddate,
    }
    //
    if (plans[adddate]) {
      //
      if (
        window.confirm(
          '\n' +
            '' +
            plans[adddate].text +
            '',
        )
      ) {
        //
        fetch(testroute + '/updateaddplandata', {
          method: 'POST',
          body: JSON.stringify(data),
          headers: {
            'Content-Type': 'application/json',
          },
          mode: 'cors',
        })
          .then(res => res.json())
          .then(str => {
            alert('')
            loadPlans()
          })
      }
      return
    }
    //
    //fetch
    fetch(testroute + '/addplandata', {
      method: 'POST',
      body: JSON.stringify(data),
      headers: {
        'Content-Type': 'application/json',
      },
      mode: 'cors',
    })
      .then(res => res.json())
      .then(str => {
        alert('')
        loadPlans()
      })
  }
  const onClickDeletePlan = date => {
    if (window.confirm('\n' + plans[date].text + '')) {
      //
      fetch(testroute + '/deleteplandata', {
        method: 'POST',
        body: JSON.stringify([date]),
        headers: {
          'Content-Type': 'application/json',
        },
        mode: 'cors',
      })
        .then(res => res.json())
        .then(str => {
          loadPlans()
        })
    }
  }
  const openModal = date => {
    setOpenModal(true)
    setChangePlandate(date)
    setChangePlantext(plans[date].text)
  }
  const closeModal = () => {
    setOpenModal(false)
  }
  const onChangereplaceText = e => {
    setChangePlantext(e.target.value)
  }
  const onClickUpdatePlan = () => {
    if (changePlantext === '') {
      alert('')
      return
    }
    const data = {
      text: changePlantext,
      date: changePlandate,
    }
    //Fetch
    fetch(testroute + '/updateaddplandata', {
      method: 'POST',
      body: JSON.stringify(data),
      headers: {
        'Content-Type': 'application/json',
      },
      mode: 'cors',
    })
      .then(res => res.json())
      .then(str => {
        loadPlans()
      })
    closeModal()
  }
  return (
    <div>
      <h1>Plans</h1>
      <div className="flame" id="plan-addflame">
        <p id="addplanTitle"></p>
        <span id="namesubtitle"></span>
        <div>
          
          <SimpleDatePicker
            onChange={date => setSelectDate(date)}
            selectdate={selectdate}
          />
        </div>
        <div>
          <input
            placeholder="()"
            className="plan-input"
            maxLength="4"
            value={addplanText}
            onChange={e => setAddplanText(e.target.value)}
          />
        </div>
        <button
          className="bluebutton"
          id="plan-addbutton"
          onClick={onClickAddPlan}
        >
          
        </button>
      </div>
      <div>
        <span></span>
        <span></span>
      </div>
      <div className="plantable-holder">
        <table className="plantable">
          <thead>
            <tr className="plantable-head">
              <td className="plantable-td-date"></td>
              <td className="plantable-td-text"></td>
              <td className="plantable-td-button"></td>
              <td className="plantable-td-button"></td>
            </tr>
          </thead>
          <tbody className="plantable-tbody">
            {plansArray.map(plan => (
              <tr key={plan.date}>
                <td className="plantable-td-date">{plan.date}</td>
                <td className="plantable-td-text">{plan.text}</td>
                <td className="plantable-td-button">
                  <button
                    className="redbutton"
                    onClick={() => onClickDeletePlan(plan.date)}
                  >
                    
                  </button>
                </td>
                <td className="plantable-td-button">
                  <button
                    className="bluebutton"
                    onClick={() => openModal(plan.date)}
                  >
                    
                  </button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
      <div>
        <Calender
          receptionDate={printDate}
          plans={plans}
          shifts={[]}
          onChange={value => {}}
          setPrintdate={value => setPrintdate(value)}
        />
      </div>
      <Modal
        isOpen={isOpenModal}
        onRequestClose={closeModal}
        style={customStyles}
        contentLabel="worktime Modal"
      >
        <h2 className="modaltitle"></h2>
        <div>
          {changePlandate}
          <br />
          <input
            value={changePlantext}
            onChange={onChangereplaceText}
            maxLength="4"
          ></input>
        </div>
        <button className="redbutton" onClick={closeModal}>
          
        </button>
        <button className="bluebutton" onClick={onClickUpdatePlan}>
          
        </button>
      </Modal>
    </div>
  )
}
export default Plans
