import React, { Component } from 'react'
import { makeStyles } from '@material-ui/core/styles'
import Input from '@material-ui/core/Input'

const useStyles = makeStyles(theme => ({
  container: {
    display: 'flex',
    flexWrap: 'wrap',
  },
  input: {
    margin: theme.spacing(1),
  },
}))

class Register extends Component {
  constructor(props) {
    super(props)
    this.state = {
      userdata: [],
      username: '',
      worktime: '',
    }
  }
  componentDidMount() {
    //
    fetch('/userdata')
      //fetch('/testuserdata')
      .then(res => res.json())
      .then(data => {
        //
        if (data.regist != null && !data.regist) {
          this.setState({ userdata: data, username: data.displayName })
        } else {
          //
          this.props.history.push('/')
        }
      })
  }
  render() {
    return (
      <div>
        <h1>Sign Up</h1>
        <div className="profileholder">
          <div className="profilesetting">
            <div className="profilecard">
              <img
                alt="profile"
                src={this.state.userdata.picture}
                id="profileimage"
              ></img>
              <p id="linename">{this.state.userdata.displayName}</p>
            </div>
            <div>
              
              <Input
                id="username"
                value={this.state.username}
                onChange={this.onChangeusername}
                className={useStyles.input}
                inputProps={{
                  'aria-label': 'username',
                }}
                placeholder="10"
              />
              <p id="namesubtitle"></p>
            </div>
            <div className="worktime">
              :
              <span>
                <input
                  type="radio"
                  name="worktime"
                  value=""
                  checked={this.state.worktime === ''}
                  onChange={() => this.setState({ worktime: '' })}
                />
                
                <input
                  type="radio"
                  name="worktime"
                  value=""
                  checked={this.state.worktime === ''}
                  onChange={() => this.setState({ worktime: '' })}
                />
                
              </span>
            </div>
            <button
              onClick={this.postdata}
              className="bluebutton"
              id="registerbutton"
            >
              
            </button>
          </div>
        </div>
      </div>
    )
  }
  onChangeusername = e => {
    this.setState({
      username: e.target.value,
    })
  }

  //
  postdata = () => {
    //
    if (this.state.username === '') {
      alert('')
    } else if (this.state.username.length > 10) {
      alert('10')
    } else {
      const data = {
        username: this.state.username,
        worktime: this.state.worktime,
      }
      //fetch('/testregister', {
      fetch('/register', {
        method: 'POST',
        body: JSON.stringify(data),
        headers: {
          'Content-Type': 'application/json',
        },
        mode: 'cors',
      })
        .then(res => res.json())
        .then(res => this.props.history.push('/Home'))
    }
  }
}

export default Register
